$yellow: #e9c46aff;
$orange: #e86a58ff;
$green: #1a6a62ff;
$dark-blue: #023047ff;
$blue: #264653ff;
$light-blue: rgb(69, 120, 141);
$dark-gray: #3b373aff;
$darker-gray: rgb(34, 32, 35);
$gray: #4d494cff;
