.main-content {
    padding-bottom: 60px;
    margin-top: 60px;
}

.email-field-error-msg,
.name-field-error-msg,
.message-field-error-msg {
    margin: 0;
    padding: 0 2%;
    display: flex;
    flex: auto;
    justify-content: flex-end;
    align-items: flex-start;
    font-size: 0.7em;
    //color: $white;
}

.form-error-message {
    min-height: 5px;
    position: relative;
    height: 100%;
}

.invalid-tooltip {
    right: 0px;
}

.form-group {
    margin-bottom: 4%;
}

.message-form-group {
    margin-bottom: 8%;
}

.form-control {}

body>.container {
    padding-top: 60px;
}